<template>
  <div id="app">
    <div class="shopee-log">
      <transition name="logo-transition">
        <shopee-icon v-show="logoVisible" class="bg-log" :svg="shopeeLogo"></shopee-icon>
      </transition>
      <transition name="info-transition">
        <div class="logo-info" v-show="logoVisible">
          <shopee-icon class="logo" :svg="lightLogo"></shopee-icon>
          <div class="logo-title">Enterprise Design System</div>
          <div class="logo-desc">Enterprise Design System 是Shopee面向企业产品和数字体验的设计系统，<br/>提供高效、轻量且一致的产品体验</div>
        </div>
      </transition>
    </div>
  </div>
</template>
<script>
import ShopeeLogo from './shopee-cart.svg';
import LightLogo from './shopee-s.svg';
export default {
  name: 'App',
  data () {
    return {
      logoVisible: false,
      shopeeLogo: ShopeeLogo,
      lightLogo: LightLogo,
    }
  },
  mounted () {
    this.logoVisible = true;
  }
}
</script>
<style>
#editor {
  margin: auto;
  width: 80%;
  height: 580px;
}
</style>



